import axios from 'axios'

//todo change API_ADDRESS
export const API_ADDRESS = process.env.NODE_ENV === 'development' ? 'https://webportal.postok.co.kr/api' : window.location.origin + '/api'
//export const API_ADDRESS = process.env.NODE_ENV === 'development' ? 'http://localhost:4000/api' : 'https://webportal.postok.co.kr/api'
// ���� url :window.location.origin, ���� localhost:3000
//export const API_ADDRESS = 'localhost:4000' + '/api'
//
const instanceAxios = axios.create({
  baseURL: API_ADDRESS,
  withCredentials: false,
})

instanceAxios.defaults.headers.common['Authorization'] = `Bearer ${window.localStorage.getItem('token')}`

export default instanceAxios
